import React, { useEffect } from 'react';
import Typography from '../../../../components/atoms/Typography';
import Picture from '../../../../components/atoms/Picture';
import { withNamespaces } from 'react-i18next';
import { plyrVideo } from '../../../../utils/video';
import { withSitecoreContext, Text } from '@sitecore-jss/sitecore-jss-react';
import Title from '../../../../components/atoms/Title';
import Button from '../../../../components/atoms/Button';
import { pushAnaylyticsData, createDynamicLabels } from '../../../../../utils/analytics';
import { analyticsData } from '../../../../../constants/Analytics/analytics-data';
import { get } from 'lodash';
import useStyles from '../style';
import aspectRatios from '../../../../utils/AspectRatios';
import DctSvgIcon from '../../../../components/atoms/Svg';
import { isInsideChina } from '../../../../utils/cookie';
import YoukuPlayer from '../../../../components/molecules/YoukuPlayer';
import NoSsr from '@material-ui/core/NoSsr';
import { canUseDOM } from '../../../../utils/canUseDOM';

const videoKey = 'video';
const Slide = props => {
  const { componentName, pageName, isParalax = true } = props;
  const {
    image,
    link,
    mobileImage,
    segmentText,
    title,
    video,
    variant = {},
    youkuVideo,
    fallbackImage,
  } = get(props, 'slide.fields');
  let playerDOMRef = null;
  const classes = useStyles();
  const isVideo = variant.value && variant.value.toLowerCase() === videoKey;
  let hasVideo = isVideo && get(video, 'value.href', '') && get(video, 'value.href', '').length > 0;
  const hasInsideChina = isInsideChina(props.sitecoreContext);
  //This will check user is inside china or chinese language is selected.
  if (hasInsideChina) {
    //check if variant is video and youku player url is there.
    hasVideo = isVideo && get(youkuVideo, 'value.href', '') && get(youkuVideo, 'value.href', '').length > 0;
  }

  const anayltyicsTitle = createDynamicLabels([pageName, componentName, `${get(props, 'sitecoreContext.route.name', '')}-${title && title.value}`]);

  useEffect(() => {
    if (canUseDOM && get(props.sitecoreContext, 'route', false) && hasVideo && !isInsideChina(props.sitecoreContext)) {
      let plyrDOM = document.querySelector('.plyr-video');
      function loadPlyr() {
        if (props.isCSR) {
          if (props.canVideoInit) {
            plyrVideo({
              playerDOMRef: playerDOMRef,
              posterImage: image.value.src,
              analyticsData: analyticsData.headerInteraction.videoPlayClick,
              titleText: anayltyicsTitle,
              shouldAnimateElm: true,
              withGradient: true,
              shouldShowPoster: false,
            });
          }
        } else {
          plyrVideo({
            playerDOMRef: playerDOMRef,
            posterImage: image.value.src,
            analyticsData: analyticsData.headerInteraction.videoPlayClick,
            titleText: anayltyicsTitle,
            shouldAnimateElm: true,
            withGradient: true,
            shouldShowPoster: false,
          });
        }
      }
      const plyrDOMInterval = setInterval(() => {
        plyrDOM = document.querySelector('.plyr-video');
        if (plyrDOM) {
          clearInterval(plyrDOMInterval);
          document.addEventListener('load', loadPlyr());
        }
      }, 500);
    }
  }, [hasVideo, props.canVideoInit, get(props.sitecoreContext, 'route', false), isInsideChina(props.sitecoreContext)]);

  const addAnalyticsInfo = () => {
    const obj = {
      ...analyticsData.pageBodyInteraction.arrowClick,
      ...{ label: createDynamicLabels([pageName, componentName, `${title.value} - ${link.value.text}`]) },
    };
    pushAnaylyticsData(obj);
  };

  return (
    <div className={classes.rtlClass}>
      {hasVideo ? (
        hasInsideChina ? (
          <YoukuPlayer
            {...{
              media: {
                image,
                mobileImage,
                aspectRatio: aspectRatios['16x9'],
              },
              scrollToTopNeeded: true,
              playIconClass: classes.playIcon,
              zIndex: 9,
              sitecoreContext: props.sitecoreContext,
              youkuVideo,
              fallbackImage,
              anayltyicsTitle,
              componentName,
              pageName,
              alternateAltText: title && title.value,
            }}
          />
        ) : (
          <>
            <Picture
              className="pictureWithPoster"
              media={{
                image,
                mobileImage,
                aspectRatio: aspectRatios['16x9'],
                disableLazyLoad: true,
              }}
              alternateAltText={title && title.value}
              isParalax={isParalax}
            />
            <div className="gradient-bg"></div>
            <NoSsr>
              <div
                className="plyr__video-embed plyr-video"
                data-poster={image.value.href}
                ref={ref => (playerDOMRef = ref)}
                data-plyr-embed-id={video.value.href}
                data-plyr-provider="youtube"
              >
              </div>
              <Button
                iconOnly
                color="inherit"
                className="close-btn plyr__element--hide"
              >
                <DctSvgIcon name="CloseIcon" />
              </Button>
            </NoSsr>
          </>
        )
      ) : (
        <>
          <Picture
            media={{
              image,
              mobileImage,
              aspectRatio: aspectRatios['16x9'],
              disableLazyLoad: true,
            }}
            alternateAltText={title && title.value}
          />
          <div className="gradient-bg"></div>
        </>
      )}
      <div className={`${classes.slide} slide`}>
        <Title variant="body2" text={segmentText} className={classes.white} />
        <Typography variant="h1" component="h1" className="heading">
          <Text field={title} />
        </Typography>
        {link && link.value && link.value.text &&
          <Button
            onClick={addAnalyticsInfo}
            component="span"
            buttonType="primary"
            field={link}
          />
        }
      </div>
    </div>
  );
};

export default withSitecoreContext()(withNamespaces()(Slide));
