import React, { useEffect, useState, useRef } from 'react';
import { withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';
import Picture from '../../../components/atoms/Picture';
// import Container from '../../../components/molecules/Container';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import { useTheme } from '@material-ui/core/styles';
import '../../../../styles/themes/slick-themes.css';
import { settings } from './config';
import Slide from './Slide';
import { get } from 'lodash';
import useStyles from './style';
import { COMPONENT_NAME } from './locators';
import LiveWeatherInfoContainer from '../../../components/organism/LiveWeatherInfo';
// import { isIE } from '../../../utils/isIEBrowser';
// import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';

import fetchService from '../../../../services/FetchService';
import { canUseDOM } from '../../../utils/canUseDOM';
import aspectRatios from '../../../utils/AspectRatios';
import {
  getComponentName,
  getPageName,
} from '../../../utils/getPageComponentInfo';

import {
  setSessionStorage,
  getSessionStorage,
} from '../../../../utils/sessionStorage';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import {
  pushAnaylyticsData,
  createDynamicLabels,
} from '../../../../utils/analytics';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const HeroCarousel = props => {
  const { params, appConfig = {} } = props;
  const pageName = getPageName(props);
  const componentName = getComponentName(props);
  const uid = get(props, 'rendering.uid', '');
  const language = get(props, 'sitecoreContext.language', '');
  const slides = get(props, 'fields.carouselItems');
  const [slidesData, setSlidesData] = useState(slides);
  const [canVideoInit, setCanVideoInit] = useState(false);
  const [initialSlideState, setInitialSlideState] = useState(0);
  const [componentIndex, setComponentIndex] = useState(Math.random() * 10);
  const isCSR = Boolean(params.isCSR);
  const theme = useTheme();
  const isRTL = theme && theme.direction === 'rtl';
  const sliderRef = useRef(null);

  useEffect(() => {
    if (typeof window !== 'undefined') {
      gsap.registerPlugin(ScrollTrigger);
    }
    if (canUseDOM) {
      window.addEventListener(
        'orientationchange',
        () => {
          const handleOrientation = setInterval(() => {
            var isVideoPlaying = document
              .querySelector('.hero-banner-section .slick-slider')
              .classList.contains('fullscreen');
            if (!isVideoPlaying) {
              clearInterval(handleOrientation);
              setComponentIndex(Math.random() * 10);
            }
          }, 100);
        },
        false
      );
    }
  }, []);

  // This will fetch personalized data for Component on browser
  useEffect(() => {
    // we need to convert isCSR to boolean because we are getting
    // isCSR as "true"
    if (isCSR && canUseDOM && language) {
      const cSRSessionContent = getSessionStorage(`csr-${uid}-${language}`);

      if (Boolean(cSRSessionContent)) {
        setSlidesData(JSON.parse(cSRSessionContent));
        setCanVideoInit(true);
      } else {
        try {
          fetchService({
            url: `${window &&
              window.location
                .origin}/sitecore/api/layout/render/csr/?item=%2F&sc_lang=${language}&sc_apikey=${appConfig.sitecoreApiKey
              }&uid=${uid}`,
            method: 'GET',
          }).then(res => {
            const csrComponent = res && res.data && res.data[0];
            if (csrComponent) {
              setSessionStorage(
                `csr-${uid}-${language}`,
                JSON.stringify(csrComponent.carouselItems)
              );
              setSlidesData(csrComponent.carouselItems);
              setCanVideoInit(true);
            }
          });
        } catch (err) {
          console.error(err);
        }
      }
    }
  }, [get(props, 'sitecoreContext.language', '')]);

  const addSwipeAnalyticsInfo = index => {
    const obj = {
      ...analyticsData.pageBodyInteraction.swipeArrowClick,
      ...{
        label: createDynamicLabels([
          pageName,
          componentName,
          get(slidesData[index], 'fields.title.value', ''),
        ]),
      },
    };
    pushAnaylyticsData(obj);
  };

  // eslint-disable-next-line react/display-name
  settings.customPaging = i => {
    const pagingIndex = isRTL ? slidesData && slidesData.length - 1 - i : i;
    return isCSR && !canVideoInit ? (
      <div className={classes.fallbackColor} />
    ) : (
      <a
        href="#"
        aria-label={slidesData[pagingIndex].fields.thumbnailImage.value.alt}
      >
        <Picture
          media={{
            image: slidesData[pagingIndex].fields.thumbnailImage,
            aspectRatio: aspectRatios['3x2'],
            disableLazyLoad: true,
          }}
          alternateAltText={slidesData[pagingIndex].fields.title.value}
          isParalax={false}
        />
      </a>
    );
  };

  settings.rtl = isRTL;

  const classes = useStyles();

  return (
    <React.Fragment>
      <div
        data-locator={COMPONENT_NAME}
        className={`hero-banner-section ${classes.root} ${componentIndex} `}
        key={componentIndex}
      >
        <Slider
          afterChange={i => {
            setInitialSlideState(i);
            addSwipeAnalyticsInfo(i);
          }}
          initialSlide={initialSlideState}
          ref={sliderRef}
          onInit={(...props) => {
            // Animating first load apperance.
            // let slickList = ;
            // let activeImage = sliderRef.current.innerSlider.list.querySelector('.slick-active .picture-tag--using-isAdvanced')[0];
            // if (activeImage) {
            //   gsap.set(activeImage, {
            //     opacity: 0,
            //     scale: 1.05,
            //   });
            //   setTimeout(() => {
            //     gsap.timeline().to(activeImage, {
            //       opacity: 1,
            //       duration: '500ms',
            //     }).to(activeImage, {
            //       scale: 1,
            //       duration: '1000ms',
            //     }, '-=500');
            //   }, 2000);
            // }
          }}
          {...settings}
        >
          {slidesData &&
            slidesData.map((item, i) =>
              isCSR && !canVideoInit ? (
                <div />
              ) : (
                <Slide
                  key={i}
                  slide={item}
                  isCSR={isCSR}
                  canVideoInit={canVideoInit}
                  componentName={componentName}
                  pageName={pageName}
                  isParalax={false}
                />
              )
            )}
        </Slider>
        <LiveWeatherInfoContainer
          className="weather-widget"
          language={language}
        />
        {/* <h1>Hero carosuel Onload1</h1>
        <h1>Hero carosuel Onload2</h1>
        <h1>Hero carosuel Onload3</h1>
        <h1>Hero carosuel Onload</h1>
        <h1>Hero carosuel Onload</h1> */}
      </div>
      <div className={classes.botSpace} />
    </React.Fragment>
  );
};

export default withErrorBoundary(withSitecoreContext()(HeroCarousel));
