import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  fallbackColor: {
    height: '100%',
    background: theme.palette.background.disabledGrey,
  },
  heading: {
    ...theme.palette.common.ellipsis(),
  },
  rtlClass: {
    direction: theme.direction,

    '& .close-btn': {
      display: 'none',
    },
  },
  botSpace: {
    height: pxToRem(24),
    background: theme.palette.common.White,
    marginBottom: pxToRem(48),
    [theme.breakpoints.up('lg')]: {
      marginBottom: theme.spacing(8),
      height: pxToRem(55),
    },
  },
  root: {
    position: 'relative',
    '& .weather-widget': {
      zIndex: 5,
      position: 'absolute',
      transition: 'all 750ms cubic-bezier(0.94, 0.02, 0.59, 0.97)',
      ...theme.mixins.left('1.5rem'),
      bottom: pxToRem(120),
      direction: 'ltr',
      '&.hidden': {
        transform: 'translateX(-100px)',
        opacity: '0',
      },
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.right('5.5%'),
        ...theme.mixins.left('auto'),
        bottom: '5rem',
      },
      '@media (max-width: 850px) and (orientation: landscape)': {
        ...theme.mixins.right('5.5%'),
        ...theme.mixins.left('auto'),
        bottom: theme.spacing(10.4),
      },
    },
    '& .video-stopped .plyr__poster': {
      opacity: '1',
    },
    '& .plyr__element--hide,& .plyr--playing .gradient-bg': {
      display: 'none',
    },
    '& .close-btn': {
      color: theme.mixins.Black(),
      position: 'absolute',
      top: theme.spacing(3),
      ...theme.mixins.right(theme.spacing(3)),
      zIndex: '10',
      width: pxToRem(40),
      height: pxToRem(40),
      background: theme.mixins.White(900),
      borderRadius: '50%',
      cursor: 'pointer',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',

      [theme.breakpoints.up('lg')]: {
        top: theme.spacing(4),
        ...theme.mixins.right(theme.spacing(4)),
        width: pxToRem(56),
        height: pxToRem(56),
      },

      '& svg': {
        height: theme.spacing(2),
        width: theme.spacing(2),
        [theme.breakpoints.up('lg')]: {
          height: theme.spacing(4),
          width: theme.spacing(4),
        },
      },

      '&:hover': {
        borderColor: 'transparent',
        background: theme.mixins.White(),
      },

      '&.plyr__element--hide': {
        display: 'none',
      },
    },
    '& .gradient-bg': {
      background:
        'linear-gradient(180deg, rgba(0,0,0,0.35) 0%, rgba(0,0,0,0.19511554621848737) 21%, rgba(0,0,0,0.10547969187675066) 52%, rgba(0,0,0,0.4) 74%, rgba(0,0,0,0.45) 100%)',
      zIndex: '2',
      height: '100vh',
      width: '100vw',
      position: 'absolute',
      ...theme.mixins.left(0),
      top: '0',
      transition: 'all 300ms ease-out',
    },
    '& .slick-initialized': {
      '&.fullscreen': {
        height: '100vh',
        '& .slick-track': {
          height: '100vh',
        },
        '& .slide-dots': {
          bottom: '-14rem',
        },
      },
    },

    '&  [dir="ltr"] .slick-slide': {
      float: 'left',
    },

    '& .slick-initialized .slick-slide': {
      transition: 'height 750s cubic-bezier(0.94, 0.02, 0.59, 0.97)',
      position: 'relative',
      height: 'calc((var(--vh, 1vh) * 100) - (23px + var(--headerHeight)))',
      [theme.breakpoints.up('lg')]: {
        height: 'calc((var(--vh, 1vh) * 100) - (45px + var(--headerHeight)))',
      },
      [theme.breakpoints.up('xxl')]: {
        height: 'calc((var(--vh, 1vh) * 100) - (55px + var(--headerHeight)))',
      },

      '& picture': {
        height: '100vh',
        position: 'inherit',
      },
      '& .img-to-animate-wrapper': {
        height: '100vh',
        '& picture': {
          height: '100%',
          width: '100%',
        },
      },

      '& .slide, & .plyr__control--overlaid': {
        visibility: 'hidden',
        opacity: 0,
      },

      '&.slick-active': {
        '& .slide, & .plyr__control--overlaid': {
          visibility: 'visible',
          opacity: 1,
        },
        '& .plyr__control--overlaid:hover': {
          background: theme.mixins.Black(500),
          transition: 'background 0.3s ease-in-out',
        },
        '& .slide.hidden, & .plyr--playing .plyr__control--overlaid': {
          visibility: 'hidden',
          opacity: 0,
        },
        '& .plyr__control--overlaid.plyr__element--hide': {
          opacity: '0',
          visibility: 'hidden',
        },
      },
      '&.fullscreen': {
        height: '100vh',
      },
      '& img': {
        width: '100%',
        height: '100%',
      },
    },
    '& .slick-thumb li': {
      borderTop: '5px solid rgba(0, 0, 0, 0.4)',
      animation: '500ms ease-out slideInFromBottom',
      width: 'auto',
      height: 'auto',
      flex: '1',
      overflow: 'hidden',
      maxHeight: pxToRem(100),
      [theme.breakpoints.up('md')]: {
        width: pxToRem(150),
      },
      [theme.breakpoints.up('lg')]: {
        width: pxToRem(192),
        height: pxToRem(113),
        flex: '0 0 auto',
        maxHeight: 'initial',
      },
    },
    '& .slick-thumb': {
      display: 'flex !important',
      transition: 'all 750ms cubic-bezier(0.94, 0.02, 0.59, 0.97)',
      '&.hidden': {
        transform: 'translateY(65px)',
      },
      [theme.breakpoints.up('lg')]: {
        justifyContent: 'flex-end',
      },
      '& li': {
        transition: 'filter 1s ease',
        filter: 'grayscale(100%)',
      },
      '& img': {
        width: '100%',
        height: '100%',
        display: 'inherit',
      },
      '& :not(.img-to-animate-wrapper) img, & .img-to-animate-wrapper': {
        transition: 'all 1s ease',
        [theme.breakpoints.up('lg')]: {
          '&:hover': {
            transform: 'scale(1.1, 1.1)',
          },
        },
      },
    },
    '& .slick-thumb .slick-active': {
      filter: 'grayscale(0%)',
    },
    '& .slick-dots .slick-active': {
      borderTopColor: '#fff',
    },
    '& .slick-dots': {
      width: 'auto',
      height: 'auto',
      bottom: pxToRem(-24),
      ...theme.mixins.right(0),
      [theme.breakpoints.down('sm')]: {
        ...theme.mixins.right(0),
        transform: 'none',
        width: '100%',
      },
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.marginRight('5.5%'),
        bottom: theme.spacing(-7),
      },
      '& li': {
        margin: '0',
      },
      '&.hidden': {
        bottom: '-7.5rem',
      },
    },

    '& .plyr': {
      height: '100vh',
      [theme.breakpoints.up('lg')]: {
        height: 'calc(100vh - 100px)',

        '&.plyr--playing, &.plyr--paused': {
          height: '100vh',
        },
        '&.plyr--stopped': {
          height: 'calc(100vh - 100px)',
        },
        '&.video-stopped, &.plyr--paused.video-stopped': {
          height: 'calc(100vh - 100px)',
        },
      },
      '& iframe, .plyr__controls': {
        opacity: 0,
        visibility: 'hidden',
      },
      '& iframe': {
        height: '100vh',
      },
      '& .plyr__video-wrapper': {
        height: '100%',
      },

      '& .plyr__poster': {
        backgroundSize: 'cover',
      },
    },
    '& .plyr__control--overlaid': {
      width: theme.spacing(6),
      height: theme.spacing(6),
      left: 'calc(50% - 10px)',
      bottom: pxToRem(70),
      transform: `translate(calc(50% - 48px), calc(-50% - ${pxToRem(1)}))`,
      display: 'flex',
      background: 'transparent',
      border: `${pxToRem(2)} solid #fff`,
      zIndex: '10',
      transition: 'all 750ms cubic-bezier(0.94, 0.02, 0.59, 0.97)',
      padding: '0',
      top: theme.spacing(-4),

      '@media (max-width: 850px) and (orientation: landscape)': {
        top: theme.spacing(8),
        ...theme.mixins.left('auto'),
        ...theme.mixins.right(theme.spacing(-11)),
      },

      [theme.breakpoints.up('lg')]: {
        padding: pxToRem(34),
        bottom: theme.spacing(36),
        ...theme.mixins.left('auto'),
        width: theme.spacing(13.5),
        height: theme.spacing(13.5),
        top: theme.spacing(8),
        // important is requirement to override its native css
        ...theme.mixins.right(`${theme.spacing(-30.5)} !important`),
        transform: 'none',
      },

      '& svg': {
        margin: 'auto',
        ...theme.mixins.positionLR(pxToRem(1), 'auto'),
        height: pxToRem(18),
        width: pxToRem(18),
        // we need these important propery to
        // reset its position
        right: '0 !important',
        [theme.breakpoints.up('lg')]: {
          height: theme.spacing(5),
          width: theme.spacing(5),
        },
      },
      '&:hover': {
        backgroundColor: 'transparent',
      },
    },
  },
  ieSpecific: {
    '& .weather-widget': {
      [theme.breakpoints.up('lg')]: {
        bottom: '12.5rem',
      },
    },

    '& .slick-dots': {
      justifyContent: 'flex-end',
      position: 'relative',
      marginBottom: theme.spacing(-14),
      top: theme.spacing(-7),

      '& li': {
        flex: 'auto 0 0',

        '& img': {
          left: 0,
        },
      },
    },
  },
  slide: {
    position: 'absolute',
    width: '80%',
    // adding 36 to compensate thumbnail
    // negetive position
    bottom: pxToRem(192),
    ...theme.mixins.left('2rem'),
    zIndex: '10',
    color: theme.mixins.White(),
    transform: 'translateX(0)',
    transition: 'all 750ms cubic-bezier(0.94, 0.02, 0.59, 0.97)',
    '&.hidden': {
      transform: 'translateX(-100px)',
      opacity: '0',
    },
    [theme.breakpoints.up('lg')]: {
      paddingRight: theme.spacing(9),
      width: '60%',
      bottom: pxToRem(71),
      ...theme.mixins.left('5.5%'),
    },
    '@media (max-width: 850px) and (orientation: landscape)': {
      ...theme.mixins.left('5.5%'),
      ...theme.mixins.paddingRight(theme.spacing(6)),
      bottom: theme.spacing(10.4),
      width: '65%',
    },
    '& .heading': {
      marginBottom: pxToRem(17),
      minHeight: pxToRem(48),
      [theme.breakpoints.up('lg')]: {
        minHeight: pxToRem(145),
      },
    },
    '& .MuiButton-root': {
      borderTopColor: theme.mixins.White(),
      borderTop: theme.borders.divider,
      color: theme.mixins.White(),

      '&:hover': {
        '& .MuiButton-endIcon': {
          color: theme.mixins.White(),
          backgroundColor: theme.mixins.Black(),
        },
      },
      '& .MuiButton-endIcon': {
        color: theme.mixins.Black(),
        backgroundColor: theme.mixins.White(),
      },
    },
  },
  white: {
    color: '#fff',
    '&:before': {
      backgroundColor: '#fff',
    },
    [theme.breakpoints.up('lg')]: {
      letterSpacing: pxToRem(0.83),
    },
  },
  weatherWidget: {
    position: 'absolute',
    ...theme.mixins.left('1.5rem'),
    direction: 'ltr',
    bottom: pxToRem(64),
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.right('5.5%'),
      ...theme.mixins.left('auto'),
      bottom: '5rem',
    },
  },
  playIcon: {
    position: 'absolute',
    width: pxToRem(48),
    height: pxToRem(48),
    top: 'auto',
    bottom: theme.spacing(36),
    ...theme.mixins.positionLR('auto', '37%'),
    marginTop: pxToRem(-109 / 2),
    ...theme.mixins.marginLeft(pxToRem(-109 / 2)),
    color: theme.mixins.White(),
    borderWidth: pxToRem(2),
    borderColor: theme.mixins.White(),
    borderStyle: 'solid',
    zIndex: 8,
    transition: 'all 750ms cubic-bezier(0.94, 0.02, 0.59, 0.97)',
    transform: `translate(calc(50% - ${pxToRem(
      48
    )}), calc(-50%  - ${theme.spacing(3.125)}))`,
    '& svg': {
      width: pxToRem(18),
      height: pxToRem(18),
      position: 'relative',
      left: pxToRem(2),
    },
    [theme.breakpoints.up('lg')]: {
      width: pxToRem(109),
      height: pxToRem(109),
      bottom: theme.spacing(36),
      ...theme.mixins.positionLR('auto', theme.spacing(44)),
      transform: 0,
      '& svg': {
        width: theme.spacing(5),
        height: theme.spacing(5),
      },
      '&:hover': {
        background: theme.mixins.Black(500),
        transition: 'background 0.3s ease-in-out',
      },
    },
    '&.hidden': {
      transform: 'translateX(-100px)',
      opacity: '0',
    },
  },
}));

