//   root: {
//     width: '100%',
//     height: '100%',
//     background: 'red',
//     position: 'relative',
//     paddingBottom: '20%',
//     height: 0,
//   },
//   '& .slick-list, .slick-slider': {
//     margin: '30px auto 50px',
//   },
// }));
export default useStyles;
