import { createSelector } from 'reselect';

const getLiveWeatherData = state => {
  return state;
};

export const selectLiveWeatherData = createSelector(
  getLiveWeatherData,
  data => data.LiveWeatherInfoComponentReducer
);

export const getSelectedTemperature = createSelector(
  getLiveWeatherData,
  data => data.LiveWeatherInfoComponentReducer.selectedTemperature
);
