import React from 'react';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';

import LiveWeatherInfo from './LiveWeatherInfo';
import { getLiveWeatherInfoStart } from '../LiveWeatherInfo/redux/actions';
import { selectLiveWeatherData } from '../LiveWeatherInfo/redux/selectors';

const LiveWeatherInfoContainer = props => {
  return <LiveWeatherInfo {...props} />;
};
export const mapDispatchToProps = dispatch => {
  return {
    getLiveWeatherInfoStart: language =>
      dispatch(getLiveWeatherInfoStart(language)),
  };
};

export const mapStateToProps = createStructuredSelector({
  liveWeatherData: selectLiveWeatherData,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LiveWeatherInfoContainer);
