import React, { useEffect } from 'react';
import get from 'lodash/get';

import Box from '../../../components/molecules/Box';
import DctSvgIcon from '../../atoms/Svg';

import useStyles from './style';
import ICON_MAPPING from './iconMapping';
import Typography from '../../atoms/Typography';

const LiveWeatherInfo = props => {
  const { liveWeatherData, className, language } = props;
  const city = get(liveWeatherData, 'data.results.data.location.city', '');
  const imageName = get(
    liveWeatherData,
    'data.results.data.imageName',
    ''
  );
  const celciusTemperature = get(
    liveWeatherData,
    'data.results.data.temperatureC',
    ''
  );
  const classes = useStyles();
  const { getLiveWeatherInfoStart } = props;
  useEffect(() => {
    if (language) {
      getLiveWeatherInfoStart(language);
    }
  }, [language]);
  return (
    celciusTemperature && (
      <div className={`${classes.liveWeatherInfo} ${className}`}>
        {ICON_MAPPING[imageName] && (
          <Box component="span">
            <DctSvgIcon name={ICON_MAPPING[imageName]} />
          </Box>
        )}
        <Typography
          component="span"
          variant="body1"
          className={classes.subscribeDescription}
        >
          {celciusTemperature}&deg;C
        </Typography>
        <Typography
          component="span"
          variant="body1"
          className={classes.subscribeDescription}
        >
          {city}
        </Typography>
      </div>
    )
  );
};

export default LiveWeatherInfo;
